.contact-table {
  font-size: 1rem;
  margin: 1rem auto;
  /* background-color: #e2e2e2; */
}

form {
  /* width: 90%; */
  margin: 0 auto;
}

@media only screen and (min-width: 800px) {
  form {
    width: 80%;
  }

  .contact-table {
    font-size: 1.25rem;
  }
}

fieldset {
  border-radius: 8px;
  border: 2px solid black;
  background-color: #ebf6e0;
  background-color: #e2e2e2;
  box-shadow: 2px 0 15px rgb(2, 10, 117);
}

legend {
  margin: 0 auto;
  margin-top: 1rem;
  color: #fff;
  background-color: #000;
  padding: 0.5rem 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
}

label {
  display: inline-block;
  font-size: 1rem;
  text-transform: uppercase;
  margin: 0.25rem;
}

input,
textarea {
  color: #666;
  font-family: "PT Serif, serif";
  font-size: 1.25rem;
  display: block;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  width: 100%;
  border: 3px solid rgb(170, 82, 252);
  /* border-bottom: 3px solid blueviolet; */
}

textarea {
  height: 7em;
  resize: none;
  overflow: auto;
}
